<template>
  <div>
    <form class="pure-form pure-form-aligned"
          v-if="queryType !== 'all'">
      <fieldset>
        <legend v-if="queryType === 'user'">查询指定账户信息</legend>
        <legend v-else>查询指定账户历史记录</legend>

        <div class="pure-control-group">
          <label for="username">用户名</label>
          <input type="text" id="username" name="username" disabled
                 :value="username">
        </div>
        <div class="pure-control-group">
          <label for="operatename">账户名</label>
          <input type="text" id="operatename" name="operatename" placeholder="账户名"
                 v-model="operatename">
        </div>

        <div class="pure-controls">
          <input type="submit" value="提交" class="pure-button pure-button-primary"
                 @click.prevent="query">
        </div>
      </fieldset>
    </form>

    <p v-if="queryType === 'all'">所有账户信息</p>
    {{queryResult}}
  </div>
</template>

<script>
  import axios from 'axios'

  const apiQuery = `/query`
  const apiHistory = `/history`

  export default {
    name: "Query",

    props: ['queryType'],

    data: function () {
      return {
        username: '',
        requesttype: '',
        operatename: '',
        queryResult: ''
      }
    },

    methods: {
      query() {
        const self = this
        let api = this.queryType === 'history' ? apiHistory : apiQuery

        axios.post(api, {
          username: self.username,
          requesttype: self.requesttype,
          operatename: self.operatename
        })
          .then(res => {
            if (!res.ifsuccessful) return alert(res.errinfo)

            self.queryResult = res
          })
          .catch(err => {
            console.log(api, err)
            alert('网络错误，请重试。')
          })
      }
    },

    created() {
      this.username = localStorage.getItem('username')

      if (this.queryType === 'all') {
        this.requesttype = 'queryAll'
        this.query()
      } else if (this.queryType === 'user') {
        this.requesttype = 'queryUser'
      } else {
        this.requesttype = 'gethistory'
      }
    }
  }
</script>

<style scoped>

</style>



