<template>
  <div>
    <form class="pure-form">
      <fieldset>
        <legend>注册上链</legend>
        <input type="text" id="username" name="username" placeholder="用户名"
               v-model="username">
        <input type="submit" value="提交" class="pure-button pure-button-primary"
               @click.prevent="enroll">
      </fieldset>
    </form>
  </div>
</template>

<script>
  import axios from 'axios'

  const apiEnroll = `/enroll`

  export default {
    name: "Enroll",

    data: function() {
      return {
        username: '',
        requesttype: 'initUser'
      }
    },

    methods: {
      enroll() {
        const self = this

        axios
        .post(apiEnroll, {
          username: self.username,
          requesttype: self.requesttype
        })
        .then(res => {
          if (!res.ifsuccessful) return alert(res.errinfo)

          localStorage.setItem('username', this.username)
          alert('注册成功！')
        })
        .catch(err => {
          console.log(apiEnroll, err)
          alert('网络错误，请重试。')
        })
      }
    }
  }
</script>

<style scoped>

</style>



