<template>
  <form class="pure-form pure-form-aligned">
    <fieldset>
      <legend>交易</legend>

      <div class="pure-control-group">
        <label for="username">用户名</label>
        <input type="text" id="username" name="username" disabled
               :value="username">
      </div>
      <div class="pure-control-group">
        <label for="operatename">发往账户</label>
        <input type="text" id="operatename" name="operatename" placeholder="账户名"
               v-model="operatename">
      </div>
      <div class="pure-control-group">
        <label for="mount">交易数量</label>
        <input type="text" id="mount" name="mount" placeholder="交易数量"
               v-model="amount">
      </div>
      <div class="pure-controls">
        <input type="submit" value="提交" class="pure-button pure-button-primary"
               @click.prevent="trade">
      </div>
    </fieldset>
  </form>
</template>

<script>
  import axios from 'axios'

  const apiTrade = `/trade`

  export default {
    name: "Trade",

    data: function () {
      return {
        username: '',
        amount: '',
        requesttype: 'invoke'
      }
    },

    methods: {
      trade() {
        const self = this
        axios
          .post(apiTrade, {
            username: self.username,
            operatename: self.operatename,
            operateamount: self.amount,
            requesttype: self.requesttype
          })
          .then(res => {
            if (!res.ifsuccessful) return alert(res.errinfo)
            alert('交易成功！')
          })
          .catch(err => {
            console.log(apiTrade, err)
            alert('网络错误，请重试。')
          })
      }
    },

    created() {
      this.username = localStorage.getItem('username')
    }
  }
</script>

<style scoped>

</style>



